#forumZoekenForm .InputField {
  margin: 0.5em;
  display: flex;
  align-items: baseline;
}

#forumZoekenForm .forumZoekenGeavanceerd {
  position: absolute;
  background-color: #E53D00;
  border-radius: 5px;
  display: none;
  margin-top: 60px;
}

#forumZoekenForm .forumZoekenGeavanceerd .form-inline {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.breadcrumbs {
  display: flex;
  align-items: baseline;
  justify-content: flex-end;
}

.breadcrumbs > * {
  margin: 1ex;
}

.breadcrumbs select {
  width: auto;
}

.forumdeel {
  background-color: #ce4e15;
  box-shadow: none;
}

#forumtabel {
  table-layout: fixed;
}

#forumtabel .bericht {
  text-align: left;
}

#forumPosten .verborgen {
  display: none;
}

#forumPosten #forumForm {
  text-align: left;
}

#forumPosten #forumForm .markItUpEditor {
  color: black;
  font-size: initial;
}

#forumPosten #forumForm .markItUpHeader ul li.markItUpButton {
  margin: 4px;
  cursor: pointer;
}

#forumPosten #forumForm .markItUpHeader ul li.markItUpButton:hover {
  border: 0;
}

#forumPosten #berichtPreview {
  color: black;
}

#forumPosten #berichtPreview strong {
  color: black;
}

.bericht0 .citaat {
  background-color: #414262;
}

.bericht1 .citaat {
  background-color: #353670;
}

.draad-readonly {
  font-style: italic;
}

.draad-gesloten {
  font-style: italic;
}

.draad-verwijderd,
.post-verwijderd {
  font-weight: bold;
  color: red;
}

.moment {
  font-size: .9em;
  color: #bbb;
  cursor: default;
}

.forum-header,
.forum-footer {
  display: flex;
  flex-wrap: wrap;
}

.forum-header h1,
.forum-footer h1 {
  width: 100%;
}

.forum-draad {
  display: flex;
  flex-direction: column;
}

.forum-draad .tussenschot {
  height: 2px;
  margin: 0;
  padding: 0;
  background-color: #d3d3d3;
}

.forum-draad .tussenschot.ongelezenvanaf {
  background-color: #e8cf2a;
}

.forum-draad .forum-post,
.forum-draad .forum-posten {
  display: flex;
  flex-direction: row;
}

.forum-draad .forum-post .forumpostKnoppen,
.forum-draad .forum-posten .forumpostKnoppen {
  min-height: 70px;
}

.forum-draad .forum-post .forumpostKnoppen a,
.forum-draad .forum-posten .forumpostKnoppen a {
  border: none;
  background: none;
  float: left;
  padding: 2px;
  margin: 2px;
  width: 22px;
  height: 22px;
}

.forum-draad .forum-post .forumpostKnoppen .forummodknop,
.forum-draad .forum-posten .forumpostKnoppen .forummodknop {
  display: none;
}

.forum-draad .forum-post .forum-bericht,
.forum-draad .forum-posten .forum-bericht {
  flex: 1;
  /* grow */
  margin: 0;
  padding: .2em .6em 10px;
  vertical-align: top;
  text-align: justify;
}

.forum-draad .forum-post .forum-bericht .bewerkt,
.forum-draad .forum-posten .forum-bericht .bewerkt {
  font-size: .8em;
  color: #bbb;
}

.forum-draad .forum-post .forum-bericht .bewerkt a,
.forum-draad .forum-posten .forum-bericht .bewerkt a {
  color: #bbb;
}

.forum-draad .forum-post .forum-bericht .bewerkt hr,
.forum-draad .forum-posten .forum-bericht .bewerkt hr {
  margin-bottom: 0;
  text-align: left;
  background-color: #bbb;
}

.forum-draad .forum-post .forum-bericht.bericht0,
.forum-draad .forum-posten .forum-bericht.bericht0 {
  background-color: #353670;
}

.forum-draad .forum-post .forum-bericht.bericht1,
.forum-draad .forum-posten .forum-bericht.bericht1 {
  background-color: #414262;
}

.forum-draad .forum-post .meldingen,
.forum-draad .forum-posten .meldingen {
  flex: 0;
  margin: .2em;
}

#ongelezen {
  visibility: hidden;
  position: relative;
  top: -100px;
}

#draad-melding {
  display: none;
  background-color: #ffffc1;
}

#bewerk-melding {
  display: none;
  background-color: #ffffc1;
}

#public-melding {
  color: #fff;
  background-color: #f66;
}

#draad-melding,
#bewerk-melding,
#public-melding {
  margin-bottom: 5px;
  padding: 4px 8px 4px 4px;
  text-align: left;
}

/* Paginering */

span.curpage {
  padding: 4px 5px;
  font-weight: bold;
  background-color: #e8cf2a;
  color: #000;
}

.forum-paginering {
  padding: 5px;
  margin-left: 160px;
}

.forum-paginering a {
  padding: 4px 5px;
}

.forum-paginering a:hover {
  background-color: #ddd;
  text-decoration: none;
}

.forum-paginering .curpage {
  background-color: initial;
}

/* Modereren */

#modereren {
  display: none;
  border: 1px solid #999;
  margin: 10px;
  padding: 10px;
}

#modereren table {
  width: 100%;
}

#modereren table input[type="text"] {
  width: 300px;
}

#modereren label {
  display: inline-block;
  width: 120px;
  text-align: right;
}

/* Zoeken */

#forumZoekenForm {
  float: right;
}

#forumZoekenForm #field_zoekopdracht {
  width: 150px;
  background-image: url("/plaetjes/famfamfam/find.png");
  background-repeat: no-repeat;
  background-position: center right;
  padding-right: 16px;
}

#forumZoekenForm .forumZoekenGeavanceerd {
  right: 250px;
  padding: 25px;
}

#forumZoekenForm .forumZoekenGeavanceerd .InputField {
  display: inline-block;
}

/* Forum deel */

.auteur {
  flex: 0 0 200px;
  /* vast */
  padding: 4px;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto auto 1fr;
}

.auteur .forumpostKnoppen {
  display: none;
}

.auteur .postpijl {
  grid-column: 2;
  grid-row: 1;
  justify-self: end;
  align-self: start;
}

.auteur .postpijl a {
  border: 0;
}

.auteur .postpijl a:hover {
  color: black;
  text-decoration: none;
}

.auteur .naam,
.auteur .moment,
.auteur .forumpostKnoppen {
  grid-column: 1;
}

.auteur .forumpasfoto {
  grid-column: 2;
  grid-row-start: 2;
  grid-row-end: 4;
}

.forum-deel {
  display: grid;
  grid-template-columns: auto 1fr auto;
}

.forum-deel .alternate-row {
  padding: 0.7em;
}

.forum-deel .alternate-row:nth-child(6n +1),
.forum-deel .alternate-row:nth-child(6n + 2),
.forum-deel .alternate-row:nth-child(6n + 3) {
  background-color: #353670;
}

.forum-deel .paging,
.forum-deel .forumdeel-omschrijving,
.forum-deel .forum-posten,
.forum-deel .forum-reageren {
  grid-column: 1 / span 3;
}

.forum-deel .paging,
.forum-deel .header {
  font-weight: bold;
  color: #fff;
  background-color: #222348;
  padding: 1em;
}

.forum-deel .paging a,
.forum-deel .paging a:hover,
.forum-deel .header a,
.forum-deel .header a:hover {
  color: white;
}

.forumdeel-omschrijving {
  padding: 10px;
  margin: 10px 0;
}

.forumheadbtn {
  float: right;
  margin-right: 15px;
}

/* Forum Categorie */

.forumcategorie {
  clear: left;
}

.forumcategorie h3 a {
  color: #000;
}

.forumcategorie h3 a:hover {
  text-decoration: none;
  cursor: default;
}

.forumcategorie p.forumcategorie-omschrijving,
.forumcategorie p.forumdeel-omschrijving {
  text-align: justify;
}

.forumcategorie .forumdelen {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

.forumcategorie .forumdelen .forumdeel {
  flex: 0 1 20%;
  padding: 8px 12px;
  margin: 10px;
}

.forumcategorie .forumdelen .forumdeel h3 {
  margin: 0.5em 0 1em 0;
}

.forumcategorie .forumdelen .forumdeel hr {
  background-color: #ccc;
  border-bottom: 1px solid #fff;
}

.forumcategorie .forumdelen .forumdeel h4 {
  margin-top: 0.5em;
  text-align: center;
}

@media screen and (max-width: 1280px) {
  .auteur {
    flex: 0 0 160px;
  }
}

@media screen and (max-width: 960px) {
  .modfuncties {
    font-size: 0;
  }

  .forumcategorie .forumdelen .forumdeel {
    flex: 0 1 100%;
  }

  .forum-deel .forum-deel-draad,
  .forum-deel .forum-deel-titel {
    flex-direction: column;
  }

  .forum-draad,
  .forum-deel {
    width: auto;
    clear: both;
  }

  .forum-draad .forum-post,
  .forum-draad .forum-posten,
  .forum-deel .forum-post,
  .forum-deel .forum-posten {
    flex-direction: column;
  }

  .forum-draad .forum-paginering,
  .forum-deel .forum-paginering {
    margin-left: 0;
  }

  .auteur {
    flex: 1 0;
    grid-template-columns: 1fr auto auto;
    border-bottom: 2px solid #d3d3d3;
  }

  .auteur .postpijl {
    padding: 0 10px;
    align-self: end;
    grid-row: 2;
    grid-column: 3;
  }

  .auteur .postpijl .postlink {
    display: block;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }

  .auteur .postpijl .postanchor {
    padding-top: 44px;
    margin-top: -44px;
  }

  .auteur .forumpasfoto {
    grid-row-start: 1;
  }

  .auteur .forumpostKnoppen {
    min-height: 0;
  }

  .auteur .forumpostKnoppen a {
    opacity: 1 !important;
  }
}

